import {connect} from "react-redux";
import Users from "./Users";
import React from "react";
import axios from "axios"
import {searchUsersAC, selectPageAC, setUsersAC, updateSearchTextAC} from "../../../../redux/usersPageReducer";

class UsersContainerAPI extends React.Component {

    componentDidMount() {
        axios.get(`https://social-network.samuraijs.com/api/1.0/users?page=1&count=${this.props.pageSize}${this.props.searchInput !== "" ? `&term=${this.props.searchInput}` : ""}`).then(
            (response) => {

                this.props.setUsers(response.data.items, response.data.totalCount, 1)
            }
        )
    }

    onPageChanged = (page) => {
        this.props.selectPage(page)
        debugger
        axios.get(`https://social-network.samuraijs.com/api/1.0/users?page=${page}&count=${this.props.pageSize}${this.props.searchInput !== "" ? `&term=${this.props.searchInput}` : ""}`).then(
            (response) => {
                this.props.setUsers(response.data.items, response.data.totalCount, page)
            }
        )
    }

    onSearchInputUpdate = (event) => {
        this.props.updateSearchText(event.target.value)
    }

    onSearchUsers = () => {
        this.props.searchUsers()
        axios.get(`https://social-network.samuraijs.com/api/1.0/users?page=1&count=${this.props.pageSize}${this.props.searchInput !== "" ? `&term=${this.props.searchInput}` : ""}`).then(
            (response) => {
                if (response.data.items.length === 0) alert("–ù–∏—á–µ–≥–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ") //TODO normal message
                this.props.setUsers(response.data.items, response.data.totalCount, 1)
            }
        )
    }

    render() {
        return <Users usersCount={this.props.usersCount}
                      pageSize={this.props.pageSize}
                      currentPage={this.props.currentPage}
                      users={this.props.users}
                      onSearchUsers={this.onSearchUsers}
                      onSearchInputUpdate={this.onSearchInputUpdate}
                      searchInput={this.props.searchInput}
                      selectPage={this.onPageChanged}/>
    }
}


const mapStateToProps = (state) => {
    return {
        usersCount: state.usersPage.totalCount,
        pageSize: state.usersPage.pageSize,
        currentPage: state.usersPage.currentPage,
        users: state.usersPage.items,
        searchInput: state.usersPage.searchString
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        selectPage: (page) => dispatch(selectPageAC(page)),
        setUsers: (users, totalCount, currentPage) => dispatch(setUsersAC(users, totalCount, currentPage)),
        updateSearchText: (text) => dispatch(updateSearchTextAC(text)),
        searchUsers: () => dispatch(searchUsersAC())
    }
}

const UsersContainer = connect(mapStateToProps, mapDispatchToProps)(UsersContainerAPI)

export default UsersContainer