const SET_USERS = 'SET-USERS'
const SELECT_PAGE = 'SELECT-PAGE'
const SEARCH_USERS = 'SEARCH-USERS'
const UPDATE_SEARCH_TEXT = 'UPDATE-SEARCH-TEXT'

const initialState = {
    items: [],
    currentPage: 1,
    totalCount: 5199,
    pageSize: 10,
    searchString: ""
}

export const userPageReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USERS: {
            return {
                items: action.users,
                currentPage: action.currentPage,
                totalCount: action.totalCount,
                pageSize: state.pageSize,
                searchString: state.searchString
            }
        }
        case SELECT_PAGE: {
            return {
                ...state,
                currentPage: action.page,
                pageSize: state.pageSize,
                totalCount: state.totalCount,
                searchString: state.searchString
            }
        }
        case UPDATE_SEARCH_TEXT: {
            return {
                ...state,
                searchString: action.text
            }
        }
        case SEARCH_USERS: {
            return {
                ...state,
            }
        }
        default:
            return state
    }
}

export const setUsersAC = (users, totalCount, currentPage) => (
    {type: SET_USERS, users, totalCount, currentPage}
)
export const selectPageAC = (page) => ({type:SELECT_PAGE, page})
export const updateSearchTextAC = (text) => ({type:UPDATE_SEARCH_TEXT, text})
export const searchUsersAC = () => ({type:SEARCH_USERS})

